.user{
    width: 100%;
    height: auto;
    background-color: white;
    border-radius: 10px;
    box-sizing: border-box;
    padding: 10px 20px;
    display: flex;
}
.view{
    width: 120px;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 8px;
}
.photoContainer{
    background-color: darksalmon;
    width: 100px;
    height: 100px;
    border-radius: 100px;
   
}
.buttonContainer{
width: 90%;
}
.button{
    width: 100%;
    height: 30px;
    background-color:azure;
    color: rgb(19, 19, 19);
    text-transform: uppercase;
    font-weight: 600;
    z-index: 3px;
    cursor: pointer;
}
.preInfo{
 width: 100%;
 height: 100%;
 display: flex;
 flex-direction: column;
}
.info{
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    margin-bottom: 5%;
}
.preInfo *{
    font-size: 16px;
    font-family: Arial, Helvetica, sans-serif;
}
.userStatus{
    margin-left: 12px;
    color: darksalmon;
}
